<template>
  <div class="intro">
    <div class="text-h2 font-weight-bold">{{ title }}</div>
    <div class="text-h4 mt-5">{{ subtitle }}</div>
    <v-btn
      color="#2a1e96"
      height="70"
      width="180"
      class="mt-5 text-h5"
      v-if="isShowBtn"
      to="/customProject"
    >
      
    </v-btn>
  </div>
</template>

<script>
import pubsub from "pubsub-js";
export default {
  data() {
    return {
      intro2: "",
      typeName: "",
      endTime: "",
      hours: "",
      minutes: "",
      beginTime: "",
      state: "",
    };
  },
  computed: {
    title() {
      if (this.$route.path === "/studyProject") {
        return this.typeName;
      } else {
        return this.$route.meta.title;
      }
    },
    subtitle() {
      if (this.$route.path === "/studyProject") {
        return this.endTime;
      } else {
        return this.$route.meta.subtitle;
      }
    },
    isShowBtn() {
      return this.$route.meta.isShowBtn;
    },
  },
  methods: {
    selectTimezone() {
      var d = new Date();
      //1970
      var len = d.getTime();
      //
      var timezone = -d.getTimezoneOffset() / 60;
      //GMT
      var offset = d.getTimezoneOffset() * 60000;
      //
      var utcTime = len + offset;
      return new Date(utcTime + 3600000 * timezone);
    },
    calTime() {
      var n = this.selectTimezone();
      var chazhi = this.beginTime - n;
      this.minutes = Math.floor((chazhi / 1000 / 60) % 60);
      this.hours = Math.floor((chazhi / 1000 / 60 / 60) % 24);
      if (this.hours > 0) {
        if (this.state === "start") {
          this.endTime = `${this.hours}${this.minutes}`;
        } else if (this.state === "end") {
          this.endTime = `${this.hours}${this.minutes}`;
        }
      } else {
        if (this.state === "start") {
          this.endTime = `${this.minutes}`;
        } else if (this.state === "end") {
          this.endTime = `${this.minutes}`;
        }
      }
      if (chazhi > 0) {
        setTimeout(() => {
          this.calTime();
        }, 1000);
      } else {
        this.$router.go(0);
      }
    },
  },
  mounted() {
    this.pid = pubsub.subscribe(
      "judgeTime",
      (msg, [timeArr, typeNameArr, len]) => {
        try {
          timeArr.forEach((item, index) => {
            var hhmm = item.split("-");
            var strTime1 = hhmm[0].split(":");
            var strTime2 = hhmm[1].split(":");
            var b = this.selectTimezone();
            var e = this.selectTimezone();
            var n = this.selectTimezone();
            b.setHours(strTime1[0]);
            b.setMinutes(strTime1[1]);
            b.setSeconds(0);
            b.setMilliseconds(0);
            e.setHours(strTime2[0]);
            e.setMinutes(strTime2[1]);
            e.setSeconds(0);
            e.setMilliseconds(0);

            if (b.getTime() - n.getTime() > 0) {
              this.typeName = "";
              console.log(b);
              this.beginTime = b;
              this.state = "start";
              this.calTime();
              localStorage.removeItem("dayEnd");
              throw new Error("end");
            } else if (
              n.getTime() - b.getTime() >= 0 &&
              n.getTime() - e.getTime() < 0
            ) {
              this.typeName = typeNameArr[index];
              this.beginTime = e;
              this.state = "end";
              this.calTime();
              localStorage.removeItem("dayEnd");
              throw new Error("end");
            } else if (index === len - 1 && n.getTime() - e.getTime() >= 0) {
              this.typeName = "";
              this.endTime = "~";
              localStorage.setItem("dayEnd", "end");
              throw new Error("end");
            }
          });
        } catch (e) {
          if (e.message !== "end") {
            throw e;
          }
        }
      }
    );
  },
  beforeDestroy() {
    pubsub.unsubscribe(this.pid);
  },
};
</script>

<style scoped lang="less">
.intro {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
</style>