<template>
  <div>
    <div class="text-h4 mt-4">,</div>
    <v-dialog transition="dialog-bottom-transition" max-width="600">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="#2a1e96"
          height="70"
          width="180"
          class="mt-5 text-h5"
          v-bind="attrs"
          v-on="on"
        >
          
        </v-btn>
      </template>
      <template v-slot:default="dialog">
        <v-card>
          <v-toolbar color="#2a1e96" dark class="text-h5"></v-toolbar>
          <v-textarea
            solo
            name="input-7-4"
            auto-grow
            label=""
            v-model="summaryText"
          ></v-textarea>
          <v-card-actions class="justify-end">
            <v-btn text @click="submit"></v-btn>
            <v-btn text @click="dialog.value = false"></v-btn>
          </v-card-actions>
        </v-card>
      </template>
    </v-dialog>
  </div>
</template>

<script>
import pubsub from "pubsub-js";
export default {
  data() {
    return {
      summaryText: "",
    };
  },
  computed: {
    task() {
      return this.$store.state.dataListOptions.typeList.length;
    },
    subject() {
      return localStorage.getItem("kemuLens");
    },
  },
  methods: {
    submit() {
      pubsub.publish("summary", this.summaryText);
    },
  },
  mounted() {
    this.$route.meta.title = ` ${this.task}`;
    this.$route.meta.subtitle = `${this.subject}`;
  },
  beforeRouteEnter(to, from, next) {
    if (to.path === "/summary") {
      if (localStorage.getItem("dayEnd") === "end") {
        next();
      } else {
        alert("");
      }
    }
  },
};
</script>

<style>
.alertBox {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>