import vuerouter from 'vue-router'
import customProject from '../components/customProject.vue'
import studyProject from '../components/studyProject.vue'
import summary from '../components/summary.vue'
import timeBar from '../components/timeBar.vue'
export default new vuerouter({
    routes:[
        {
            path:'/',
            meta:{title:'',subtitle:'',isShowBtn:true,currentIndex:'home'}
        },
        {
            path:'/studyProject',
            component:studyProject,
            meta:{currentIndex:0},
        },
        {
            path:'/summary',
            component:summary,
            meta:{currentIndex:1},
        },
        {
            path:'/customProject',
            component:customProject,
            meta:{title:'',currentIndex:2},
            children:[{
                name:'timeBar',
                path:'timeBar/:people',
                component:timeBar,
                meta:{title:'',subtitle:''}
            }]
        },
    ]
})